.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}

.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}

.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #2f405d;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.boton-azul-block:hover {
  background-color: #27354c;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #922B21;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.boton-rojo-block:hover {
  background-color: #7d251c;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}

.dark-mode p, .dark-mode th, .dark-mode td {
  color: #FFFFFF;
}

.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}

.dark-mode .propiedad, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}

.dark-mode .propiedad .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}

.dark-mode .propiedad .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}

.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}

.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}

.dark-mode .formulario input:not([type=submit]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

.dark-mode .dashboard__header {
  background-color: #333333;
}

.dark-mode .dashboard__sidebar {
  background-color: #333333;
}

@media (min-width: 768px) {
  .dark-mode .dashboard__sidebar {
    background-color: #4d4d4d;
  }
}
.dark-mode .table__thead {
  background-color: #4d4d4d;
}

.dark-mode .table__tr:nth-of-type(even) {
  background-color: #666;
}

/** Media Queries **/
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}

.header.inicio {
  background-position: center center;
  background-size: cover;
}

@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
}

@media (min-width: 768px) {
  .contenido-header h1 {
    max-width: 60rem;
  }
}
.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}

.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.footer .contenido-footer {
  padding-top: 2.5rem;
}

.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .derecha {
    display: flex;
  }
}
.derecha .dark-mode-boton {
  margin-left: 2.7rem;
  width: 4rem;
  filter: invert(100%);
  margin-top: 1rem;
}

@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin: 0 0 1rem 1rem;
    order: 2;
  }
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
}

@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.2s ease-in-out, opacity 0.2s linear, height 0.2s linear;
}

@media (min-width: 768px) {
  .navegacion {
    order: 1;
    height: auto;
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  display: block;
  text-align: center;
  font-size: 2.2rem;
}

@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #71B100;
}

.mobile-menu {
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}

.icono img {
  height: 10rem;
}

.icono h1 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-propiedades {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.propiedad {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  padding: 0;
  margin-bottom: 2rem;
}

.propiedad .contenido-propiedad {
  padding: 2rem;
}

.propiedad h3, .propiedad p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  padding: 0;
}

.iconos-caracteristicas li {
  display: flex;
  justify-content: center;
}

.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}

.iconos-caracteristicas li p {
  font-weight: 700;
}

.no-webp .img-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .img-contacto {
  background-image: url("../img/encuentra.webp");
}

.img-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img-contacto h2, .img-contacto p {
  color: #FFFFFF;
}

.img-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
}

.img-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}

@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}

.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.informacion-meta span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  padding: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
  border-radius: 2rem;
}

.testimonial blockquote {
  position: relative;
  padding-left: 5rem;
}

.testimonial blockquote:before {
  content: "";
  background-image: url(../img/comilla.svg);
  position: absolute;
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}

.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 0;
}

.formulario legend {
  font-size: 2rem;
  color: #333333;
}

.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}

.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 0.01rem solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.formulario textarea {
  height: 20rem;
}

.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.formulario__acciones {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.formulario__acciones a {
  padding-top: 2.5rem;
}

/** Media Queries **/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

@media (min-width: 768px) {
  .texto-nosotros blockquote {
    margin: 0;
  }
}